function DragDrop(props) {

  const classes = useStyles();
  const [selectedFile, setSelectedFile] = useState();
  const [preview, setPreview] = useState();
  // const [data, setData] = useState();
  const [image, setImage] = useState(false);
  const [isLoading, setIsloading] = useState(false);
  const {data, setData} = props;
  let confidence = 0;

  const sendFile = async () => {
    if (image) {
        console.log("hello")
      let formData = new FormData();
      formData.append("file", selectedFile);
      let res = await axios({
        method: "post",
        url: process.env.REACT_APP_API_URL,
        data: formData,
      });
      if (res.status === 200) {
        setData(res.data);
      }
      setIsloading(false);
    }
  };

  const clearData = () => {
    setData(null);
    setImage(false);
    setSelectedFile(null);
    setPreview(null);
  };

  useEffect(() => {
    if (!selectedFile) {
      setPreview(undefined);
      return;
    }
    const objectUrl = URL.createObjectURL(selectedFile);
    setPreview(objectUrl);
  }, [selectedFile]);

  useEffect(() => {
    if (!preview) {
      return;
    }
    setIsloading(true);
    sendFile();
  }, [preview]);

  const onSelectFile = (files) => {
    if (!files || files.length === 0) {
      setSelectedFile(undefined);
      setImage(false);
      setData(undefined);
      return;
    }
    setSelectedFile(files[0]);
    setData(undefined);
    setImage(true);
  };

  if (data) {
    confidence = (parseFloat(data.confidence) * 100).toFixed(2);
  }

  return (
    <>
      <div>
        <Grid item xs={12}>
          <Card
            className={`${classes.imageCard} ${
              !image ? classes.imageCardEmpty : ""
            }`}
          >
            {image && (
                <div style={{position:"relative"}}>
                  {data && (<button className="cut" onClick={clearData}>X</button>)}

              <CardContent style={{ margin:"10px", borderRadius:"20px"}}>
                <CardMedia
                  className={`${classes.media}`}
                  image={preview}
                  component="image"
                  title="Contemplative Reptile"
                />
              </CardContent >
              {isLoading && (
                <div class="ocrloader">
                <p>Scanning</p>
                <em></em>
                <span></span>
              </div>
              )}
              
              </div>
            )}
            {!image && (
              <CardContent className={classes.content}>
                <DropzoneArea
                  acceptedFiles={["image/*"]}
                  dropzoneText={
                    "Drag and drop an image of a Cotton plant leaf to process"
                  }
                  onChange={onSelectFile}
                />
              </CardContent>
            )}
            {data && (
              <CardContent className={classes.detail }>

                
                <TableContainer
                  component={Paper}
                  className={classes.tableContainer}
                >

                {
                  data.isLeaf === "leaf" && 

                  <Table
                    className={classes.table}
                    size="small"
                    aria-label="simple table"
                  >
                    <TableHead className={classes.tableHead}>
                      <TableRow className={classes.tableRow}>
                        <TableCell className={classes.tableCell1}>
                          Label:
                        </TableCell>
                        <TableCell align="right" className={classes.tableCell1}>
                          Confidence:
                        </TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody className={classes.tableBody}>
                      <TableRow className={classes.tableRow}>
                        <TableCell
                          component="th"
                          scope="row"
                          className={classes.tableCell}
                        >
                          {data.class}
                        </TableCell>
                        <TableCell align="right" className={classes.tableCell}>
                          {confidence}%
                        </TableCell>
                      </TableRow>
                    </TableBody>
                  </Table>

                }

                  {/* <Table
                    className={classes.table}
                    size="small"
                    aria-label="simple table"
                  >
                    <TableHead className={classes.tableHead}>
                      <TableRow className={classes.tableRow}>
                        <TableCell className={classes.tableCell1}>
                          Label:
                        </TableCell>
                        <TableCell align="right" className={classes.tableCell1}>
                          Confidence:
                        </TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody className={classes.tableBody}>
                      <TableRow className={classes.tableRow}>
                        <TableCell
                          component="th"
                          scope="row"
                          className={classes.tableCell}
                        >
                          {data.class}
                        </TableCell>
                        <TableCell align="right" className={classes.tableCell}>
                          {confidence}%
                        </TableCell>
                      </TableRow>
                    </TableBody>
                  </Table> */}

                {/* Mychanges */}

              {
                data.isLeaf=="not leaf" && 
                <Table
                    className={classes.table}
                    size="small"
                    aria-label="simple table"
                  >
                    
                    <TableBody className={classes.tableBody}>
                      <TableRow className={classes.tableRow}>
                        <TableCell
                          component="th"
                          scope="row"
                          className={classes.tableCell}
                        >
                          <div style={{textAlign:"center", color:"red"}}>Please Upload Cotton Leaf Image</div>
                        </TableCell>
                      </TableRow>
                    </TableBody>
                  </Table>
              }

                

                {/* MyChanges end */}

                </TableContainer>
              </CardContent>
            )}
            {/* {isLoading && (
              <CardContent className={classes.detail}>
                <CircularProgress
                  color="secondary"
                  className={classes.loader}
                />
                <Typography className={classes.title} variant="h6" noWrap>
                  Processing
                </Typography>
              </CardContent>
            )} */}
          </Card>
        </Grid>
        {/* {data && (
          <Grid item className={classes.buttonGrid}>
            <ColorButton
              variant="contained"
              className={classes.clearButton}
              color="primary"
              component="span"
              size="large"
              onClick={clearData}
              startIcon={<Clear fontSize="large" />}
            >
              Clear
            </ColorButton>
          </Grid>
        )} */}
      </div>
    </>
  );
}

export default DragDrop;
